.ActiveFilter {
    background-color: plum;
    border-color: plum;
    color: antiquewhite;
}
.ActiveFilterNone{
    background-color: #76a0cb;
    border-color: #83bbef;
    color: antiquewhite;
}
.ActiveFilter:hover {
    border-color: #645e57;
    color: antiquewhite;
    cursor: pointer;
}
.ActiveFilterNone:hover{
    border-color: #645e57;
    color: antiquewhite;
    cursor: pointer;
}
.InputError{
    border-color: red;
}